.menu-trigger,
.close-trigger {
    position: absolute;
    top: 32px;
    right: 20px;
    display: block;
    width: 42px;
    height: 42px;
    cursor: pointer;
    z-index: 333;
}

.menu-trigger:hover .menu-trigger-bar:before,
.close-trigger:hover .menu-trigger-bar:before {
    width: 100%;
}

.menu-trigger:hover .close-trigger-bar:before,
.close-trigger:hover .close-trigger-bar:before {
    width: 100%;
}

.close-trigger {
    z-index: 5;
    top: 42px;
}

.menu-trigger-bar {
    display: block;
    width: 100%;
    height: 4px;
    background-color: white;
    margin-bottom: 6px;
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
    position: relative;
}

.menu-trigger-bar:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 0%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    -webkit-transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1);
    transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1);
}

.menu-trigger-bar.top {
    width: 50%;
}

.menu-trigger-bar.middle:before {
    left: auto;
    right: 0;
}

.menu-trigger-bar.bottom {
    width: 50%;
    margin-left: 50%;
}

.close-trigger-bar {
    display: block;
    width: 100%;
    height: 4px;
    background-color: #222222;
    position: relative;
}

.close-trigger-bar:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 0%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.2);
    -webkit-transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1);
    transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1);
}

.close-trigger-bar.left {
    -webkit-transform: translateX(100px) translateY(-100px) rotate(-45deg);
            transform: translateX(100px) translateY(-100px) rotate(-45deg);
}

.close-trigger-bar.right {
    -webkit-transform: translateX(-100px) translateY(-100px) rotate(45deg);
            transform: translateX(-100px) translateY(-100px) rotate(45deg);
    top: -3px;
}

